const string =`
.skin * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.skin *::before {
  box-sizing: border-box;
}
.skin *::after {
  box-sizing: border-box;
}
.skin {
  min-height: 50vh;
  background-color: #ffe600;
  position: relative;
}
.nose {
  border: 10px solid red;
  border-color: black transparent transparent;
  position: absolute;
  top: 145px;
  left: 50%;
  margin-left: -10px;
  z-index: 2;
}
@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  33% {
    transform: rotate(5deg);
  }
  66% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.nose:hover {
  transform-origin: 50% 100%;
  animation: wave 300ms infinite linear;
}
.yuan {
  background-color: black;
  position: absolute;
  width: 20px;
  height: 6px;
  top: -16px;
  left: -10px;
  border-radius: 10px 10px 0 0;
}
.eye {
  background-color: #2e2e2e;
  border: 1px solid #030300;
  height: 64px;
  width: 64px;
  position: absolute;
  top: 100px;
  left: 50%;
  margin-left: -32px;
  border-radius: 50%;
}
.eye::before {
  position: absolute;
  content: "";
  border: 3px solid #000;
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 50%;
  background-color: #fff;
  left: 8px;
  top: 2px;
}
.eye.left {
  transform: translateX(-100px);
}
.eye.right {
  transform: translateX(100px);
}
.mouth {
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  top: 170px;
  margin-left: -100px;
}
.mouth .up {
  position: relative;
  top: -20px;
  z-index: 1;
}
.mouth .up .lip {
  background-color: #ffe600;
  border: 3px solid black;
  height: 30px;
  width: 100px;
  border-top-color: transparent;
  border-right-color: transparent;
  position: relative;
  position: absolute;
  left: 50%;
  margin-left: -50px;
}
.mouth .up .lip::before {
  content: "";
  display: block;
  width: 7px;
  height: 30px;
  position: absolute;
  bottom: 0px;
  background-color: #ffe600;
}
.mouth .up .lip.left {
  border-radius: 0 0 0 50px;
  transform: rotate(-15deg) translateX(-53px);
}
.mouth .up .lip.left::before {
  right: -6px;
}
.mouth .up .lip.right {
  border-radius: 0 0 50px 0;
  transform: rotate(15deg) translateX(53px);
}
.mouth .up .lip.right::before {
  left: -6px;
}
.mouth .down {
  height: 180px;
  position: absolute;
  top: 5px;
  width: 100%;
  overflow: hidden;
}
.mouth .down .yuan1 {
  border: 3px solid black;
  width: 150px;
  height: 1000px;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -75px;
  border-radius: 75px/300px;
  background-color: #9b000a;
  overflow: hidden;
}
.mouth .down .yuan1 .yuan2 {
  width: 200px;
  height: 300px;
  position: absolute;
  background: #ff485f;
  bottom: -155px;
  left: 50%;
  margin-left: -100px;
  border-radius: 100px;
}
.face {
  position: absolute;
  left: 50%;
  border: 3px solid black;
  width: 88px;
  height: 88px;
  top: 200px;
  margin-left: -44px;
  z-index: 3;
  border-radius: 50%;
  background-color: #ff0000;
}
.face.left {
  transform: translateX(-180px);
}
.face.right {
  transform: translateX(180px);
}
`
export default string